<template>
	<view class="wrapper">
		<view class="fixed_tab">
			<simpleTab
				:tab-list="tabList" 
				:tabCur.sync="TabCur"
				tab-class="tab_class"
				:showLine="false"
				select-class="selected"
				@change="tabChange"
				showLine
				>
			</simpleTab>
		</view>
		<view class="content">
			<swiper :current="TabCur" duration="300" @change="swiperChange" class="swiper">
				<!-- 基本信息 -->
				<swiper-item>
					<scroll-view class="list_wrapper" scroll-y="true" lower-threshold="50" @scrolltolower="scrollBottom">
						<view class="list"  v-if="!empty">
							<view class="item" v-for="(item,index) in listData" :key="index">
								<view class='desc'>
									<view class="info">
										<!-- <image src="../../static/award.png" class="avatar"></image> -->
										<view class='other'>
											<view class='name_w'>
												<text class="name">{{item.memberName}}</text>
												<text class="status unpass">{{item.orderList[0].orderStateName}}</text>
											</view>
											<view class='tel' @click="tel(item.memberMobile)">{{item.memberMobile}}</view>
										</view>
									</view>
									<view class="go" @click="goDeal(item)">
										<view class="btn">
											<view class="txt">立即处理</view>
											<image src="../../static/right_black.png"></image>
										</view>
									</view>
								</view>
								<view class="orderList">
									<view class="order_item" v-for="(litt,idx) in item.orderList" :key="idx">
										<view class="flex-row-start row">
											<view class="key">意向4S店</view>
											<view class="value text-ellipsis">{{litt.store4sName}}</view>
										</view>
										<view class="flex-row-start row">
											<view class='key'>意向车型</view>
											<view class='txt want'>{{litt.carTypeName}}</view>
											<view class='status'>
												<text class='shape'></text>
												<text class='txt'>{{litt.nodeStepName}}</text>
											</view>
										</view>
										<view class="flex-row-start row">
											<view class="key">询价情况</view>
											<view class="value">{{litt.inquiryInfo!=null?litt.inquiryInfo+' 万':''}}</view>
										</view>
									</view>
								</view>
							</view>
							<loadMore :status="status" :content-text="textDesc"></loadMore>
						</view>
						<view class="no_data" v-else>
							暂无数据
						</view>
					</scroll-view>
				</swiper-item>
				<swiper-item >
					<scroll-view class="list_wrapper" scroll-y="true" lower-threshold="50" @scrolltolower="scrollBottom">
						<view class="list"  v-if="!empty">
							<view class="item" v-for="(item,index) in listData" :key="index">
								<view class='desc'>
									<view class="info">
										<view class='other'>
											<view class='name_w'>
												<text class="name">{{item.memberName}}</text>
												<text class="status pass">{{item.orderList[0].orderStateName}}</text>
											</view>
											<view class='tel' @click="tel(item.memberMobile)">{{item.memberMobile}}</view>
										</view>
									</view>
									<view class="go" @click="goLook(item)">
										<view class="btn">
											<view class="txt">查看</view>
											<image src="../../static/right_black.png"></image>
										</view>
									</view>
								</view>
								<view class="orderList">
									<view class="order_item" v-for="(litt,idx) in item.orderList" :key="idx">
										<view class="flex-row-start row">
											<view class="key">意向4S店</view>
											<view class="value text-ellipsis">{{litt.store4sName}}</view>
										</view>
										<view class="flex-row-start row">
											<view class='key'>意向车型</view>
											<view class='txt want'>{{litt.carTypeName}}</view>
											<view class='status'>
												<text class='shape'></text>
												<text class='txt'>{{litt.nodeStepName}}</text>
											</view>
										</view>
										<view class="flex-row-start row">
											<view class="key">询价情况</view>
											<view class="value">{{litt.inquiryInfo!=null?litt.inquiryInfo+'万':''}}</view>
										</view>
									</view>
								</view>
							</view>
							<loadMore :status="status" :content-text="textDesc"></loadMore>
						</view>
						<view class="no_data" v-else>
							暂无数据
						</view>
					</scroll-view>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	import simpleTab from "../../components/simpleTab/simpleTab.vue"
	import request from "../../common/request.js"
	import {formatTime} from "../../common/tools.js"
	import loadMore from "../../components/loadMore/loadMore.vue"
	export default {
		data() {
			return {
				TabCur: 0,
				tabList: [
					{ name: '待处理' },
					{ name: '已处理' }
				],
				scrollTop:0 ,// 当linear为true的时候需要通过onpagescroll传递参数
				scrollMaxHeight:200,//滑动的高度限制，超过这个高度即背景全部显示
				paramsInfo:{
					isTreated:''
				},
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"loading",
				empty:false,
				listData:[]
			};
		},
		methods:{
			tabChange(index) {
			 	this.TabCur = index;
			},
			swiperChange(e){
			 	this.TabCur=e.detail.current
				this.listData=[]
				this.getList()
			},
			scrollBottom(){
				if(this.status!="noMore"){
					this.getList()
				}
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			goDeal(item){
				this.perfectNavigateTo({
					url:'/pages/adminControlOrder/checkOrder?orderInfo='+JSON.stringify(item)
				})
			},
			goLook(item){
				this.perfectNavigateTo({
					url:`/pages/adminControlOrder/lookPrcess?clientOrder=${JSON.stringify(item)}`
				})
			},
			getList(){
				if(this.TabCur==0){
					this.paramsInfo.isTreated=0
				}else{
					this.paramsInfo.isTreated=1
				}
				this.request("/order/admin/orders",this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data
						if(_data.length>0){ //如果有数据
							this.empty=false
							this.listData=_data
						}else{ //如果这次没数据
							this.empty=true
						}
						this.status="noMore"
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
		},
		onShow(){
			this.getList()
		},
		components:{
			simpleTab,
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
	}
</script>
<style lang="stylus" scoped>
	.wrapper
		.fixed_tab
			width:100%
			position:fixed
			top:0upx
			left:0upx
			right:0upx
		.content
			min-height:120upx
			position:fixed
			top:90upx
			left:0upx
			right:0upx
			bottom:0upx
			.list_wrapper
				padding:0upx 30upx 40upx 30upx
				height:100%
				.list
					&:before
						content:''
						display:table
					.item
						margin-top:80upx
						.desc
							display:flex
							flex-direction row
							justify-content space-between
							align-items center
							.go
								.btn
									display:flex
									flex-direction row
									align-items center
									height:40upx
									.txt
										font-size:28upx;
										font-family:PingFangSC-Medium,PingFang SC;
										font-weight:600;
										color:rgba(31,35,52,1);
										margin-right:10upx
									image
										width:26upx
										height:26upx
							.info
								display:flex
								flex-direction row
								align-items center
								.avatar
									width:108upx
									height:108upx
									border-radius 50%
									overflow hidden
									margin-right:40upx
								.other
									.name_w
										display:flex
										flex-direction row
										align-items center
										margin-bottom:10upx
										.name
											font-size:32upx;
											font-family:PingFangSC-Semibold,PingFang SC;
											font-weight:600;
											color:rgba(30,30,30,1);
											margin-right:20upx
										.status
											height:30upx;
											line-height 30upx
											text-align center
											border-radius:4upx;
											padding:2upx 10upx
											font-size:24upx
											color:#fff
											&.unpass
												background:#E17969;
											&.pass
												background:#70D097
						.orderList
							// margin-left:143upx
							margin-top:20upx
							.order_item
								margin-top:16upx
								padding:20upx
								background:rgba(248,249,252,1);
								font-size:24upx
								border-radius:8upx;
								.row
									&:not(:first-child)
										margin-top:15upx
									.key
										width:125upx
									.txt
										color:#000000
										&.desc
											margin-right:20upx
										&.want
											margin-right:20upx
									image
										width:26upx
										height:26upx
									.status
										display:flex
										flex-direction row
										flex:1
										justify-content flex-end
										align-items center
										text-overflow ellipsis;
										overflow hidden
										white-space:nowrap
										.shape
											width:8upx;
											height:8upx;
											transform rotate(45deg)
											background:linear-gradient(135deg,rgba(44,143,255,1) 0%,rgba(23,108,221,1) 100%);
											border-radius:2upx;
										.txt
											color:#0075D6
											font-size:24upx
											margin:0upx 10upx
											text-overflow ellipsis;
											overflow hidden
											white-space:nowrap
											width:95%
</style>	
<style lang="stylus">
	page{
		height:100vh
	}
	
	::-webkit-scrollbar{
		width: 0;
		height: 0;
		color: transparent;
	}

	.swiper
		height:100%
	.no_data
		text-align:center
		font-size:32upx;
		font-family:PingFangSC-Regular;
		font-weight:400;
		height:30vh;
		line-height:30vh;
		color:rgba(203,210,219,1);	
		
</style>
<style lang="stylus">
	.tab_class{
		background:#fff;
		font-size:28upx;
		font-family:PingFangSC-Light;
		color:#1e1e1e
	}
	.selected{
		font-size:36upx;
		font-family:PingFangSC-Medium;
		font-weight:bold;
		color:#1e1e1e;
		border:none!important
	}
</style>

